import React from 'react';


function Form(props) {


  return (
    <div className="row">
        <div className="col-sm-6">
      <form onSubmit={props.onAddEmployee}>
  <div className="mb-3">
    <label htmlFor="FirstName" className="form-label">First Name</label>

    <input 
    type="text" 
    className="form-control" 
    id="FirstName"
    name="firstname"
    value= {props.firstName}
    onChange={props.onFirstNameInputChange}/>

    </div>
    <div className="mb-3">
    <label htmlFor="LastName" className="Last-label">Last Name</label>

    <input 
    type="text" 
    className="form-control" 
    id="LastName" 
    name="lastname"
    value= {props.lastName}
    onChange={props.onLastNameInputChange}/>

    </div>
    <div className="mb-3">
    <label htmlFor="Department" className="Department">Department</label>

    <input 
    type="text" 
    className="form-control" 
    id="Department" 
    name="department"
    value= {props.department}
    onChange={props.onDepartmentInputChange}/>

    </div>
    <div className="mb-3">
    <label htmlFor="HireDate" className="HireDate">Hire Date</label>

    <input 
    type="date" 
    className="form-control" 
    id="HireDate" 
    name="hiredate"
    value= {props.hireDate}
    onChange={props.onHireDateInputChange}/>
    </div>
  
  <button type="submit" className="btn btn-primary">Add Record</button>
</form>
</div>
    </div>
  )
}

export default Form;
